<template>
  <el-dialog
    center
    title=""
    width="300px"
    :append-to-body="true"
    :close-on-click-modal="false"
    :model-value="cropVisible"
    custom-class="croppa-dialog"
  >
    <div style="text-align: center;">
      <div class="avatar-croppa-container">
        <vue-picture-cropper
          :boxStyle="{
            width: '100%',
            height: '100%',
            margin: 'auto',
            backgroundColor: '#f8f8f8',
          }"
          :img="originalImage"
          :options="{
            viewMode: 1, // 
            aspectRatio: 1, //  1 
          }"
        />
      </div>
      <div style="margin-top: 1em;"></div>
    </div>

    <template #footer>
      <div class="dialog-footer">
        <el-button @click="switchStatus" size="small"> </el-button>
        <el-button type="primary" @click="handleCrop" size="small"> </el-button>
      </div>
    </template>
  </el-dialog>
</template>

<script>
import User from 'lin/model/user'
import { mapActions } from 'vuex'
import { post, put } from 'lin/plugin/axios'
import VuePictureCropper, { cropper } from 'vue-picture-cropper'

export default {
  components: { VuePictureCropper },
  props: {
    originalImage: {
      type: String,
    },
    cropVisible: {
      type: Boolean,
      default: false,
    },
  },
  emits: ['switchCropVisible'],
  methods: {
    ...mapActions(['setUserAndState']),
    handleCrop() {
      // 
      const blob = cropper.getBlob()
      // 
      const file = new File([blob], 'avatar.jpg', { type: 'image/jpeg' })

      return post('/cms/file', {
        file,
      }).then(res => {
        // 
        if (!Array.isArray(res) || res.length !== 1) {
          this.$message.error(', ')
          return false
        }
        return put('/cms/user', {
          avatar: res[0].path,
        })
          .then(putRes => {
            if (putRes.code < window.MAX_SUCCESS_CODE) {
              this.$message({
                type: 'success',
                message: '',
              })
              this.switchStatus()
              // 
              return User.getInformation()
            }
            return Promise.reject(new Error(''))
          })
          .then(infoRes => {
            // eslint-disable-line
            // 
            const user = infoRes
            this.setUserAndState(user)
          })
      })
    },
    switchStatus() {
      this.$emit('switchCropVisible', false)
    },
  },
}
</script>
